//   css      cpv
export function createLevel(code) {
  // console.log("code: ", code);

  var level = "";
  switch (code.length) {
    case 1:
      level = "main";
      break;
    case 2:
      level = "main";
      break;
    case 3:
      level = "first";
      break;
    case 4:
      level = "second";
      break;
    case 5:
      level = "third";
      break;
    case 6:
      level = "four";
      break;
    case 7:
      level = "five";
      break;
    case 8:
      level = "six";
      break;
    case 9:
      level = "seven";
      break;
    default:
      level = "default";
      break;
  }
  return level;
}
//            
export const setBgColor = ({ level, theme }) => {
  // console.log(theme);
  switch (level) {
    case "main":
      return theme.colors.mainLevelColor;
    case "first":
      return theme.colors.firstLevelColor;
    case "second":
      return theme.colors.secondLevelColor;
    case "third":
      return theme.colors.thirdLevelColor;
    case "four":
      return theme.colors.materialColor;
    case "five":
      return theme.colors.materialColor;
    case "six":
      return theme.colors.materialColor;
    case "seven":
      return theme.colors.materialColor;
    default:
      return theme.colors.mainLevelColor;
  }
};

