import React from "react";
//   
import { Suspense } from "react";
import { Outlet } from "react-router-dom";
// 
import Header from "../../components/Header/Header";

const SharedLayout = () => {
  return (
    <div>
      <Header />
      <Suspense fallback={<div>Loading page...</div>}>
        <Outlet />
      </Suspense>
    </div>
  );
};

export default SharedLayout;
